
h1 {
  margin-bottom: 0px;
  margin-top: 0px;
}
a {
  text-decoration: none;
  color: gray;
  position: relative;
  top: -25px;
}
#app {
  font-family: Courier New, Courier, monospace;
  float: left;
  margin: 20px;
  padding: 50px;
  height: 480px;
  width: 370px;
  max-width: 370px;
  max-height: 480px;
  min-width: 370px;
  min-height: 480px;
  background-color: #eef;
}
.reset {
  margin-bottom: 20px;
  padding: 20px;
  font-size: 15px;
  font-family: monospace;
}
.row {
  clear: left;
}
.options {
  clear: left;
  padding-top: 30px;
  padding-bottom: 30px;
}

.cell {
  cursor: pointer;
  float: left;
  border: solid 1px black;
  width: 100px;
  height: 100px;
  text-align: center;
  line-height: 100px;
  font-size: 60px;
}
.cell:hover {
  background-color: #fee;
}
.ganhou {
  background-color: #aaffaa;
}
.ganhou:hover {
  background-color: #aaffaa;
}
